<template>

  <v-container>
    <v-row>
      <v-col>
        <v-text-field clearable :rules="rules" @keydown.enter="getUser(username)" :counter="16" v-model="username"
                      placeholder="Write username" flat label="Find user">

        </v-text-field>
      </v-col>
      <v-col cols="4" md="2">
        <v-btn block color="primary" @click="getUser(username)">Search</v-btn>
      </v-col>

    </v-row>

    <div class="text-left">
      <v-chip-group>

        <v-chip v-for="(favorite, key) in favorites" @click="getUser(favorite)"
                :key="key" color="success">
          <span v-text="favorite"></span>
        </v-chip>
        <v-chip @click.stop="dialogFavorite = true" color="primary">
          Add favorite
        </v-chip>
      </v-chip-group>

      <v-dialog
          v-model="dialogFavorite"
          max-width="290"
      >
        <v-card>

          <v-card-title class="text-h5">
            Favorites
          </v-card-title>
          <v-card-text>
            <v-chip-group column>
              <v-chip v-for="(favorite, key) in favorites" :key="key" color="red"
                      @click:close="deleteFavorite(favorite)" outlined close close-icon="mdi-delete">
                <span v-text="favorite"></span>
              </v-chip>
            </v-chip-group>
          </v-card-text>
          <v-card-title>Add favorite</v-card-title>
          <v-card-text>
            <v-text-field @keydown.enter="addFavorite" clearable ref="favoriteUsername" :counter="16"
                          v-model="favoriteUsername"
                          placeholder="Write username" flat label="Write username">

            </v-text-field>

          </v-card-text>
          <v-card-actions>

            <v-spacer></v-spacer>

            <v-btn
                color="primary darken-1"
                text
                @click="addFavorite()"
                :disabled="favorites.length===5"
            >
              Add
            </v-btn>
            <v-btn text color="error darken-1" @click="dialogFavorite = false">
              CANCEL
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </div>

    <v-card class="mt-3" :loading="isLoadingUser">
      <v-tabs v-model="tabs" show-arrows>

        <v-tabs-slider></v-tabs-slider>
        <v-tab
            href="#mobile-tabs-5-1"
            class="primary--text"
        >
          PLAYER
        </v-tab>

        <v-tab
            href="#mobile-tabs-5-2"
            class="primary--text"
        >
          GUILD
        </v-tab>

        <v-tab
            href="#mobile-tabs-5-3"
            class="primary--text"
        >
          FRIENDS
        </v-tab>
        <v-tab disabled
               href="#mobile-tabs-5-4"
               class="primary--text"
        >
          ACHIEVEMENTS (SOON)
        </v-tab>

      </v-tabs>
      <v-tabs-items v-model="tabs">
        <v-tab-item :value="'mobile-tabs-5-1'">
          <v-card flat>
            <v-container pt-0 pb-0 fluid grid-list-lg>
              <v-card-text>

                <div v-if="usernamea && usernamea.user && usernamea.online">
                  <div>
                    <div class="title mb-1" v-if="usernamea.user">
                      Stats player <span :style="{color: getColorRank(usernamea.user.rank)}">{{
                        usernamea.user.username
                      }}</span>
                    </div>

                    <div class="d-flex justify-start flex-row">
                      <v-badge :color="usernamea.online.value ? 'green' : 'red'" top overlap bordered dot>
                        <v-tooltip bottom
                                   :color="usernamea.online.value ? 'green' : 'red'">
                          <template v-slot:activator="{ on, attrs }">
                            <v-img v-bind="attrs"
                                   v-on="on"
                                   style="height: 100px; width: 100px;"
                                   :src="'//skin.vimeworld.com/head/'+usernamea.user.username+'.png'"></v-img>
                          </template>
                          <h4>{{ usernamea.user.username }}</h4>
                          <span>{{ usernamea.online.message }}</span>

                        </v-tooltip>
                      </v-badge>


                      <div class="ml-2">
                        <v-col class="ma-0 pa-0 mb-2">
                          <h4>ID: <span class="primary--text bold">{{ usernamea.user.id }}</span></h4>
                        </v-col>
                        <v-col class="ma-0 pa-0 mb-2">
                          <h4>Level: <span class="primary--text bold">{{ usernamea.user.level }}</span></h4>
                        </v-col>
                        <v-col class="ma-0 pa-0 mb-2">
                          <h4>Rank: <span v-bind:style="{ color: getColorRank(usernamea.user.rank)}">{{
                              usernamea.user.rank
                            }}</span></h4>
                        </v-col>
                        <v-col class="ma-0 pa-0 mb-2">
                          <h4>Played time: <span class="primary--text bold">{{ usernamea.user.playedSeconds }}</span>
                          </h4>
                        </v-col>
                        <v-col class="ma-0 pa-0 mb-2">
                          <h4>Last seen: <span class="primary--text bold">{{ usernamea.user.lastSeen }}</span></h4>
                        </v-col>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-else class="title">
                  {{ err }}
                </div>
              </v-card-text>
            </v-container>
          </v-card>
        </v-tab-item>
        <v-tab-item :value="'mobile-tabs-5-2'">
          <v-card flat>
            <v-container pt-0 pb-0 fluid grid-list-lg>
              <v-card-text>
                <div v-if="usernamea!=null">
                  <div class="title mb-1">
                    Guild player <span :style="{color: getColorRank(usernamea.user.rank)}"> {{
                      usernamea.user.username
                    }}</span>
                  </div>
                  <div class="d-flex justify-start flex-row" v-if="usernamea.user.guild!=null">
                    <div v-if="usernamea.user.guild.avatar_url !== null">
                      <v-img style="height: 100px; width: 100px;"
                             :src="usernamea.user.guild.avatar_url"></v-img>

                    </div>
                    <div class="ml-2">
                      <h4> ID: <span class="primary--text">{{ usernamea.user.guild.id }}</span></h4>
                      <h4> Name: <span class="primary--text">{{ usernamea.user.guild.name }}</span></h4>
                      <h4> Tag: <span class="primary--text">{{
                          usernamea.user.guild.tag != null ? usernamea.user.guild.tag : 'none'
                        }}</span>
                      </h4>
                    </div>
                  </div>
                  <div v-else>
                    <v-subheader>This player is not in a guild!</v-subheader>
                  </div>
                </div>
                <div v-else class="title">
                  {{ err }}
                </div>
              </v-card-text>
            </v-container>
          </v-card>
        </v-tab-item>
        <v-tab-item :value="'mobile-tabs-5-3'">
          <v-card flat>

            <v-container pt-0 pb-0 fluid grid-list-lg>
              <v-card-text>
                <v-expansion-panels v-if="usernamea">

                  <v-expansion-panel>

                    <v-expansion-panel-header>
                      <template v-slot:actions>
                        <v-icon color="primary">
                          $expand
                        </v-icon>
                      </template>

                      <div class="title">
                        Friends player
                        <span :style="{color: getColorRank(usernamea.user.rank)}"> {{ usernamea.user.username }}</span>
                        <span> - </span>
                        <span class="primary--text" v-if="!isLoadingFriends">{{ friends.length }}</span>
                        <v-progress-circular v-else
                                             indeterminate color="primary"
                                             v-show="isLoadingFriends"
                        ></v-progress-circular>
                      </div>

                    </v-expansion-panel-header>
                    <v-expansion-panel-content>
                      <v-row class="wrap">
                        <v-col
                            class="d-flex"
                            v-if="friends.length>0"
                            lg="6"
                        >
                          <v-text-field v-model="searchFriend" label="Search"></v-text-field>
                        </v-col>
                        <v-col
                            class="d-flex"
                            v-if="friends.length>0"
                            lg="6"
                        >
                          <v-select
                              :items="items"
                              v-model="keySelect"
                              @change="onChange(keySelect)"
                              label="Sorting"
                          ></v-select>
                        </v-col>

                        <v-col xs="12" sm="12" md="6" lg="4" :key="key2"
                               v-for="(friend,key2) in filteredList">
                          <v-card v-if="!isLoadingFriends">
                            <div class="d-flex justify-start flex-row">
                              <v-hover>
                                <template>
                                  <v-scroll-x-transition>
                                    <div class="pa-2">
                                      <v-badge :color="friend.online.value ? 'green' : 'red'" top overlap bordered dot>
                                        <v-tooltip bottom
                                                   :color="friend.online.value ? 'green' : 'red'">
                                          <template v-slot:activator="{ on, attrs }">
                                            <v-img v-bind="attrs"
                                                   v-on="on"
                                                   style="height: 100px; width: 100px;"
                                                   :src="'//skin.vimeworld.com/head/'+friend.username+'.png'"></v-img>
                                          </template>
                                          <h4>{{ friend.username }}</h4>
                                          <span>{{ friend.online.message }}</span>

                                        </v-tooltip>
                                      </v-badge>
                                    </div>
                                  </v-scroll-x-transition>
                                </template>
                              </v-hover>
                              <v-hover>
                                <template>
                                  <v-scroll-x-transition>
                                    <div class="pa-2">
                                      <h4 class="pointer"
                                          @click="getUser(friend.username)"><span
                                          v-bind:style="{ color: getColorRank(friend.rank)}">{{
                                          getNameRank(friend.rank)
                                        }}{{
                                          friend.username
                                        }}</span></h4>
                                      <h4>ID: <span class="primary--text"> {{ friend.id }}</span></h4>
                                      <h4>Level: <span class="primary--text"> {{ friend.level }}</span></h4>
                                    </div>
                                  </v-scroll-x-transition>
                                </template>
                              </v-hover>
                            </div>
                          </v-card>
                        </v-col>
                        <v-col cols="12" v-if="filteredList.length===0 && !isLoadingFriends">
                          <v-card-title class="m-0 pa-0 text-center red--text">
                            <v-icon color="red">mdi-cancel</v-icon>
                            Not found
                          </v-card-title>
                        </v-col>
                        <template v-if="isLoadingFriends">
                          <v-col cols="6">
                            <v-skeleton-loader v-if="isLoadingFriends"
                                               type="list-item-avatar, divider"></v-skeleton-loader>
                          </v-col>
                          <v-col cols="6">
                            <v-skeleton-loader v-if="isLoadingFriends"
                                               type="list-item-avatar, divider"></v-skeleton-loader>
                          </v-col>
                          <v-col xs="12" sm="12" md="6" lg="4" v-for="key3 in 25" :key="key3">
                            <v-skeleton-loader v-if="isLoadingFriends" elevation="4"
                                               type="article"
                            ></v-skeleton-loader>
                          </v-col>
                        </template>


                      </v-row>

                      <v-subheader class="mt-3" v-if="friends.length===0">This is player don't have
                        friends!
                      </v-subheader>

                    </v-expansion-panel-content>

                  </v-expansion-panel>
                </v-expansion-panels>
                <div v-else class="title">
                  {{ err }}
                </div>
              </v-card-text>
            </v-container>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-card>
    <v-snackbar centered top
                v-model="snackbar"
    >
      {{ err }}

      <template v-slot:action="{ attrs }">
        <v-btn
            color="red"
            text
            v-bind="attrs"
            @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>

</template>

<script>
const axios = require('axios').default;
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'HelloWorld',
  data: () => ({
    keySelect: null,
    tabs: null,
    rules: [v => v.length <= 16 || 'Max 16 characters'],
    dialogFavorite: false,
    // online: [],
    usernamea: null,
    color: '#333',
    isLoadingUser: false,
    isLoadingOnline: false,
    isLoadingFriends: false,
    rank: {
      'PLAYER': {rank: "Игрок", prefix: "", color: "9e9e9e", value: 0},
      'VIP': {rank: "VIP", prefix: "[VIP] ", color: "00be00", value: 1},
      'PREMIUM': {rank: "Premium", prefix: "[Premium] ", color: "00dada", value: 2},
      'HOLY': {rank: "Holy", prefix: "[Holy] ", color: "ffba2d", value: 3},
      'IMMORTAL': {rank: "Immortal", prefix: "[Immortal] ", color: "e800d5", value: 4},

      'YOUTUBE': {rank: "YouTube", prefix: "[YouTube] ", color: "fe3f3f", value: 5},
      'BUILDER': {rank: "Билдер", prefix: "[Билдер] ", color: "009c00", value: 6},

      'ORGANIZER': {rank: "Организатор", prefix: "[Организатор] ", color: "00bebe", value: 7},
      'MODER': {rank: "Модератор", prefix: "[Модер] ", color: "1b00ff", value: 8},

      'SRBUILDER': {rank: "Проверенный Билдер", prefix: "[Пр.Билдер] ", color: "009c00", value: 9},
      'DEV': {rank: "Разработчик", prefix: "[Dev] ", color: "00bebe", value: 10},

      'WARDEN': {rank: "Проверенный модератор", prefix: "[Пр. Модер] ", color: "1b00ff", value: 11},
      'MAPLEAD': {rank: "Главный билдер", prefix: "[Гл. билдер] ", color: "009c00", value: 12},

      'CHIEF': {rank: "Главный модератор", prefix: "[Гл. модер] ", color: "1b00ff", value: 13},
      'ADMIN': {rank: "Главный админ", prefix: "[Гл. админ] ", color: "00bebe", value: 14}
    },
    url: 'https://api.vimeworld.com',
    username: null,
    err: 'Enter username',
    friends: [],
    items: ['id', 'online', 'rank', 'level'],
    snackbar: false,
    searchFriend: '',
    favoriteUsername: '',
    favorites: [],
  }),
  created() {
    if (localStorage.getItem('names') != null) {
      this.favorites = JSON.parse(localStorage.getItem('names'));
    }
    console.log(this.favorites);
  },
  computed: {
    filteredList() {
      return this.friends.filter(friend => {
        return friend.username.toLowerCase().includes(this.searchFriend.toLowerCase())
      })
    }
  },
  methods: {
    contains(arr, elem) {
      for (let i = 0; i < arr.length; i++) {
        const areEqual = arr[i].toUpperCase() === elem.toUpperCase();
        if (areEqual) {
          return true;
        }
      }
      return false;
    },
    deleteFavorite(username) {
      let index = this.favorites.indexOf(username);
      if (index !== -1) {
        this.favorites.splice(index, 1);
      }
      localStorage.setItem('names', JSON.stringify(this.favorites));
    },
    addFavorite() {
      let username = this.favoriteUsername.replaceAll(' ', '');
      if (username !== '' && this.isValid(username) && this.favorites.length < 5 && !this.contains(this.favorites, username)) {

        axios.get(this.url + '/user/name/' + username, {
          headers: {
            'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
          }
        })
            .then(async (response) => {
              if (response.data.length === 0) {
                this.err = 'Player not found!';
                this.snackbar = true;
              } else if (response.data.error != null) {
                console.log(response.data);
                switch (response.data.error.error_code) {
                  case -3:
                  case -2:
                    this.err = 'Something wrong with VimeWorld API, please try again later';
                    break;
                  case -1:
                    this.err = 'Invalid username!';
                    break;
                  case 2:
                    this.err = 'The number of requests has exceeded the limit (300), please try again in a few seconds.';
                    break;
                  case 1:
                  case 3:
                  case 4:
                    this.err = 'Something is wrong, please try again later or reload the page.';
                    break;

                }
                this.snackbar = true;

              } else {
                let usernameCorrect = '';
                response.data.forEach(el => usernameCorrect = el.username);
                this.favorites.push(usernameCorrect);
                localStorage.setItem('names', JSON.stringify(this.favorites));
                this.favoriteUsername = '';
              }
            })
      } else if (this.contains(this.favorites, username)) {
        this.err = 'This is user already added to favorites!';
        this.snackbar = true;
      } else if (this.favorites.length === 5) {
        this.err = 'Size of favorites = 5!';
        this.snackbar = true;
      } else {
        this.err = 'Invalid username!';
        this.snackbar = true;
      }
    },
    onChange(a) {
      if (a === 'id') {
        this.keySelect = a;
        this.friends.sort((a, b) => b.id - a.id);
      } else if (a === 'online') {
        this.keySelect = a;

        this.friends.sort((a, b) => b.online.value - a.online.value);
      } else if (a === 'rank') {
        this.keySelect = a;


        this.friends.sort((a, b) => this.getValueRank(b.rank) - this.getValueRank(a.rank));
      } else if (a === 'level') {
        this.keySelect = a;

        this.friends.sort((a, b) => b.level - a.level);

      }
    },
    isValid(username) {
      return /^[a-zA-Z0-9_]+$/.test(username)
    },
    async getUser(value) {
      if (value != null && this.isValid(value)) {
        this.isLoadingUser = true;
        await axios.get(this.url + '/user/name/' + value, {
          headers: {
            'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
          }
        })
            .then(async (response) => {
              if (response.data.length === 0) {
                this.err = 'Player not found!';
                this.snackbar = true;
              } else if (response.data.error != null) {
                console.log(response.data);
                switch (response.data.error.error_code) {
                  case -3:
                  case -2:
                    this.err = 'Something wrong with VimeWorld API, please try again later';
                    break;
                  case -1:
                    this.err = 'Invalid username!';
                    break;
                  case 2:
                    this.err = 'The number of requests has exceeded the limit (300), please try again in a few seconds.';
                    break;
                  case 1:
                  case 3:
                  case 4:
                    this.err = 'Something is wrong, please try again later or reload the page.';
                    break;

                }
                this.snackbar = true;

              } else {

                this.isLoadingOnline = true;
                await this.getSessionPlayer(response.data);
                this.isLoadingUser = false;
                this.isLoadingOnline = false;

                this.isLoadingFriends = true;
                await this.getFriends();
                this.isLoadingUser = false;
                this.isLoadingOnline = false;
              }


            })
            .catch((error) => {
              if (error.response) {
                switch (error.response.status) {
                  case 400:
                  case 403:
                  case 404:
                    this.err = 'Invalid username!';
                    break;
                  case 500:
                    this.err = 'Something is wrong, please try again later or reload the page.';
                    break;
                  case 503:
                    this.err = 'Something wrong with VimeWorld API, please try again later';
                    break;
                  default:

                    if (error.response.data != null) {
                      this.err = error.response.data;
                    } else {
                      this.err = error.message;
                    }
                    this.isLoadingUser = false;
                    this.isLoadingOnline = false;
                }
              } else if (error.request) this.err = error.request
              else this.err = error.message;
              this.snackbar = true;
              this.isLoadingUser = false;
              this.isLoadingOnline = false;

            });

      } else {
        this.err = 'Nickname incorrect';
        this.snackbar = true;
        this.isLoadingUser = false;
        this.isLoadingOnline = false;
      }
      this.isLoadingUser = false;
      this.isLoadingOnline = false;


    }
    ,
    async getSessionPlayer(data) {
      let data2 = null;

      data.forEach(el => data2 = el);

      await axios.get(this.url + '/user/' + data2.id + '/session', {
        headers: {
          'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
        }
      })
          .then((response) => {
            this.usernamea = response.data;
          })
          .catch((error) => {
            if (!error.response.data.error.error_msg) {
              this.err = error.response.data.error.error_msg;


            } else {
              this.err = 'Error';


            }
            this.snackbar = true;
          })

    }
    ,
    async getFriends() {
      this.friends = [];

      await axios.get(this.url + '/user/' + this.usernamea.user.id + '/friends', {
        headers: {
          'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
        }
      })
          .then((response) => {
            let ids = [];
            response.data.friends.forEach(el => ids.push(el.id));
            if (ids.length !== 0) {
              this.getSessionFriend(ids);
            } else {
              console.log('success')
              this.friends = [];
            }
          })
          .catch((error) => {
            this.err = error.response.data.error.error_msg;
            this.snackbar = true;

          })


    }
    ,
    async getSessionFriend(ids) {

      await axios.post(this.url + '/user/session', ids, {
        headers: {
          'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
        }
      })
          .then((response) => {
            this.friends = response.data;
            this.friends.sort((a, b) => b.online.value - a.online.value);
            this.keySelect = 'online';
            this.searchFriend = '';

          })
          .catch((error) => {
            if (!error.response.data.error.error_msg) {
              this.err = error.response.data.error.error_msg;

            } else {
              this.err = 'Error';

            }
            this.snackbar = true;

          });

      this.isLoadingFriends = false;

    }
    ,

    sortById() {
      this.friends.sort((a, b) => b.id - a.id);
    }
    ,
    getColorRank(rank) {
      if (rank != null) {
        return '#' + this.rank[rank].color;
      } else {
        return '#333';
      }
    }
    ,
    getNameRank(rank) {
      if (rank != null) {
        return this.rank[rank].prefix;
      } else {
        return '';
      }
    }
    ,
    getValueRank(rank) {
      if (rank != null) {
        return this.rank[rank].value;
      } else {
        return 1;
      }
    }
    ,
  }
}
</script>
<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

li {
  list-style-type: none;
}

.value {
  opacity: 0.6;
}

.pointer {
  cursor: pointer;
}

.bold {
  font-weight: bold;
}
</style>
