<template>
  <v-app id="inspire">
    <v-app-bar
        app>

      <v-container class="py-0 fill-height">
        <v-toolbar-title>ELSite</v-toolbar-title>
        <v-spacer></v-spacer>
        <ColorThemeSwitch/>
        <v-spacer></v-spacer>
        <v-menu offset-y transition="scale-transition" nudge-width="195" max-height="75vh" class="overflow-auto">
          <v-progress-linear v-if="isLoading"
                             indeterminate
          ></v-progress-linear>
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-on="on" v-bind="attrs" icon>
              <v-icon @click="getStaff">
                mdi-security
              </v-icon>
            </v-btn>
          </template>
          <v-list>

            <v-subheader>
              Online staff: {{ staff.length }}
            </v-subheader>
            <div v-for="(item, index) in staff"
                 :key="index">

              <v-divider></v-divider>
              <v-list-item link @click="$refs.user.getUser(item.username)">
                <v-list-item-avatar class="rounded-sm">
                  <img :src="'//skin.vimeworld.com/head/'+item.username+'.png'" alt="">
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title :style="'color:'+ $refs.user.getColorRank(item.rank)">
                    {{ item.username }}
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    Онлайн
                  </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </div>
          </v-list>

        </v-menu>

      </v-container>
    </v-app-bar>
    <v-main>
      <HelloWorld ref="user"/>
      <v-snackbar right
                  v-model="snackbar"
      >
        {{ err }}

        <template v-slot:action="{ attrs }">
          <v-btn
              color="pink"
              text
              v-bind="attrs"
              @click.native.left="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-main>
  </v-app>
</template>

<script>
import HelloWorld from './components/HelloWorld';
import {default as axios} from "axios";
import ColorThemeSwitch from "@/components/ColorThemeSwitch";

export default {
  name: 'App',

  components: {
    ColorThemeSwitch,
    HelloWorld,
  },

  data: () => ({
    staff: [],
    isLoading: false, err: 'Enter username',
    snackbar: false,

  }),
  methods: {

    async getStaff() {
      this.isLoading = true;
      axios.get('https://api.vimeworld.com/online/staff', {
        headers: {
          'Access-Token': 'DWE9uc6VpMeuCm75mCooD3sDSVsWulC'
        }
      })
          .then((response) => {
            if (response.data != null) {
              this.staff = response.data;
            } else {
              this.staff = null;
            }
            this.isLoading = false;
          })
          .catch((error) => {
            if (error.response) {
              switch (error.response.status) {
                case 400:
                case 403:
                case 404:
                  this.err = 'Invalid username!';
                  break;
                case 500:
                  this.err = 'Something is wrong, please try again later or reload the page.';
                  break;
                case 503:
                  this.err = 'Something wrong with VimeWorld API, please try again later';
                  break;
                default:

                  if (error.response.data != null) {
                    this.err = error.response.data;
                  } else {
                    this.err = error.message;
                  }
              }
            } else if (error.request) this.err = error.request
            else this.err = error.message;
            this.isLoading = false;
            this.snackbar = true;
          });


    },
  }
};
</script>
<style>
.pointer {
  cursor: pointer;
}
</style>